'use client';

// --- NEW IMPORTS ---
import useAuth from './useAuth'; // Our new auth detector
import LoginScreen from './LoginScreen'; // Our new login page
import { auth, db } from './firebaseConfig'; // We need 'db' for Step 3
import { User } from 'firebase/auth'; // Type for the user object
// --- END NEW IMPORTS ---

import { useState, useEffect } from 'react';
import axios from 'axios';
import { BookOpen, LayoutDashboard, LogOut, Sparkles, Check, Plus, BarChart3, Loader2 } from 'lucide-react';
import { Line } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend } from 'chart.js';
import { getPersonalizedStrategy, Strategy } from './strategyEngine';
import BreathingExercise from './BreathingExercise';
import GoalTracker, { Habit } from './GoalTracker'; // Assuming GoalTracker exports Habit
import WeeklySummary, { JournalEntry } from './WeeklySummary'; // Assuming WeeklySummary exports JournalEntry
import MoodChart from './MoodChart';
import StreakTracker from './StreakTracker';

// Register Chart.js components
ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend);

// --- Type Definitions ---
type AnalysisResult = {
  sentiment: 'POSITIVE' | 'NEGATIVE' | 'NEUTRAL' | string;
  labels: string[];
  activities: string[];
};
type View = 'journal' | 'dashboard';

// ==================================================================
// 1. APP WRAPPER (This is the new Auth Guard)
// ==================================================================

export default function AppWrapper() {
  const { user, loading } = useAuth(); // Use the detector

  // Show a loading screen while Firebase checks for a user
  if (loading) {
    return (
      <div className="flex min-h-screen items-center justify-center bg-slate-50">
        <Loader2 size={32} className="text-indigo-600 animate-spin" />
        <p className="text-lg text-slate-600 ml-4">Loading Your Aura...</p>
      </div>
    );
  }

  // If loading is done and there's no user, show the Login page
  if (!user) {
    return <LoginScreen />;
  }

  // If loading is done AND we have a user, show the main app
  // We pass the user object down so the app knows who to save data for
  return <HomePage user={user} />;
}


// ==================================================================
// 2. MAIN APP COMPONENT (This is your *entire* old page.tsx)
// ==================================================================
function HomePage({ user }: { user: User }) { // <-- Notice it now receives the user
  // --- State Variables ---
  const [journalText, setJournalText] = useState('');
  const [selectedActivities, setSelectedActivities] = useState<string[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [analysis, setAnalysis] = useState<AnalysisResult | null>(null);
  const [error, setError] = useState('');
  const [showBreathingExercise, setShowBreathingExercise] = useState(false);
  
  // This state will be loaded from Firebase in Step 3
  const [streak, setStreak] = useState(0); 
  const [habits, setHabits] = useState<Habit[]>([]);
  const [journalHistory, setJournalHistory] = useState<JournalEntry[]>([]);

  const [view, setView] = useState<View>('journal');

  // --- Helper Functions ---
  const getISODate = (date: Date): string => date.toISOString().split('T')[0];

  // --- DATA LOADING (THIS WILL BE FILLED IN STEP 3) ---
  useEffect(() => {
    // In Step 3, we will add the onSnapshot listeners here
    // to load data from Firestore based on the user.uid
    
    // For now, let's load from localStorage so the app doesn't break
    // We will REMOVE this in the next step.
    const savedHabits = localStorage.getItem('aura-habits');
    if (savedHabits) setHabits(JSON.parse(savedHabits));

    const savedHistory = localStorage.getItem('aura-history');
    if (savedHistory) setJournalHistory(JSON.parse(savedHistory));

    const savedStreak = localStorage.getItem('aura-streak');
    if (savedStreak) setStreak(JSON.parse(savedStreak).streakCount || 0);

  }, [user]); // Re-run if the user changes

  // --- Handlers ---
  const handleAnalyze = async () => {
    // This function will also be updated in Step 3
    // to save data to Firestore instead of localStorage.
    if (!journalText.trim()) {
      setError('Please write something in your journal first.');
      return;
    }
    setIsLoading(true); setError(''); setAnalysis(null);
    try {
      const response = await axios.post('http://127.0.0.1:8000/analyze', { text: journalText, activities: selectedActivities });
      if (response.data.error) throw new Error(response.data.error);
      setAnalysis(response.data);

      // --- TODO (STEP 3) ---
      // We will replace this localStorage code with Firestore `addDoc`
      const newEntry = { id: new Date().toISOString(), date: new Date().toISOString(), ...response.data };
      const newHistory = [newEntry, ...journalHistory];
      setJournalHistory(newHistory);
      localStorage.setItem('aura-history', JSON.stringify(newHistory));

      // --- TODO (STEP 3) ---
      // We will replace this localStorage code with Firestore `setDoc`
      const today = getISODate(new Date());
      const yesterday = getISODate(new Date(Date.now() - 86400000));
      const savedStreak = JSON.parse(localStorage.getItem('aura-streak') || '{}');
      let newStreakCount = savedStreak.streakCount || 0;
      if (savedStreak.lastJournalDate === today) { /* No change */ } 
      else if (savedStreak.lastJournalDate === yesterday) newStreakCount += 1;
      else newStreakCount = 1;
      setStreak(newStreakCount);
      localStorage.setItem('aura-streak', JSON.stringify({ streakCount: newStreakCount, lastJournalDate: today }));

    } catch (err) {
      console.error(err);
      setError('Sorry, we couldn\'t analyze your entry. The server might be busy.');
    } finally {
      setIsLoading(false);
    }
  };

  const handleHabitUpdate = (id: string) => {
    // --- TODO (STEP 3) ---
    // We will replace this localStorage code with Firestore `setDoc`
    const updatedHabits = habits.map(h => (h.id === id && h.progress < h.target) ? { ...h, progress: h.progress + 1 } : h);
    setHabits(updatedHabits);
    localStorage.setItem('aura-habits', JSON.stringify(updatedHabits));
  };
  
  const handleHabitReset = () => {
    // --- TODO (STEP 3) ---
    // We will replace this localStorage code with Firestore `setDoc`
    const resetHabits = habits.map(h => ({ ...h, progress: 0 }));
    setHabits(resetHabits);
    localStorage.setItem('aura-habits', JSON.stringify(resetHabits));
  };


  // --- Render JSX ---
  return (
    <>
      {showBreathingExercise && <BreathingExercise onClose={() => setShowBreathingExercise(false)} />}
      
      <main className="flex min-h-screen flex-col items-center bg-slate-50 font-sans text-slate-800">
        <header className="w-full max-w-2xl px-4 pt-6">
          <div className="flex justify-between items-center">
            <h1 className="text-3xl font-bold text-indigo-800">Aura</h1>
            <div className="flex items-center gap-4">
              <StreakTracker streak={streak} />
              
              {/* NEW LOGOUT BUTTON */}
              <button 
                onClick={() => auth.signOut()} 
                title="Log Out"
                className="p-2 rounded-full hover:bg-slate-200 transition-colors"
              >
                <LogOut size={20} className="text-slate-600"/>
              </button>

            </div>
          </div>
        </header>

        <nav className="w-full max-w-2xl p-4 sticky top-0 z-10 bg-slate-50">
          <div className="flex justify-center space-x-2 rounded-lg bg-slate-200 p-1.5">
            <TabButton icon={<BookOpen size={18} />} label="Journal" isActive={view === 'journal'} onClick={() => setView('journal')} />
            <TabButton icon={<LayoutDashboard size={18} />} label="Dashboard" isActive={view === 'dashboard'} onClick={() => setView('dashboard')} />
          </div>
        </nav>

        <div className="w-full max-w-2xl p-4">
          {/* Journal View */}
          {view === 'journal' && (
            <div className="animate-fade-in space-y-6">
              <div className="bg-white p-6 rounded-2xl shadow-lg">
                <textarea
                  value={journalText}
                  onChange={(e) => setJournalText(e.target.value)}
                  placeholder="How are you feeling today?"
                  className="w-full h-40 p-3 border-slate-200 border rounded-xl focus:ring-2 focus:ring-indigo-500"
                  disabled={isLoading}
                />
                <div className="mt-4">
                  <h3 className="text-md font-semibold text-slate-700 mb-2">What did you do today?</h3>
                  <div className="flex flex-wrap gap-2">
                    {ALL_ACTIVITIES.map(activity => (
                      <button
                        key={activity}
                        onClick={() => setSelectedActivities(p => p.includes(activity) ? p.filter(a => a !== activity) : [...p, activity])}
                        className={`px-3 py-1.5 text-sm font-medium rounded-full border transition-all ${
                          selectedActivities.includes(activity)
                            ? 'bg-indigo-600 text-white border-indigo-600'
                            : 'bg-white text-slate-700 border-slate-300 hover:bg-slate-100'
                        }`}
                      >
                        {activity}
                      </button>
                    ))}
                  </div>
                </div>
                <button
                  onClick={handleAnalyze}
                  disabled={isLoading}
                  className="w-full mt-6 bg-indigo-600 text-white font-bold py-3 px-4 rounded-xl hover:bg-indigo-700 disabled:bg-slate-400 shadow-md flex items-center justify-center"
                >
                  {isLoading && <Loader2 size={20} className="animate-spin mr-2" />}
                  {isLoading ? 'Analyzing...' : 'Analyze My Feelings'}
                </button>
              </div>

              {error && <p className="text-rose-500 text-center mt-4">{error}</p>}

              {analysis && (
                <div className="bg-white p-6 rounded-2xl shadow-lg animate-fade-in">
                  <h2 className="text-2xl font-bold text-slate-800 mb-4">Your Analysis</h2>
                  <div className="flex flex-wrap gap-2">
                    <Tag label={analysis.sentiment} type="sentiment" />
                    {analysis.labels.map(label => <Tag key={label} label={label} type="label" />)}
                    {analysis.activities.map(activity => <Tag key={activity} label={activity} type="activity" />)}
                  </div>
                  <InsightCard analysis={analysis} />
                  <CorrelationCard analysis={analysis} />
                  {analysis.labels.includes("Anxiety") && (
                    <button
                      onClick={() => setShowBreathingExercise(true)}
                      className="w-full mt-4 bg-violet-600 text-white font-bold py-2 px-4 rounded-xl hover:bg-violet-700 transition-colors"
                    >
                      Try a Guided Breathing Exercise
                    </button>
                  )}
                </div>
              )}
            </div>
          )}

          {/* Dashboard View */}
          {view === 'dashboard' && (
            <div className="animate-fade-in space-y-6">
              <MoodChart history={journalHistory} />
              <GoalTracker habits={habits} onUpdate={handleHabitUpdate} onReset={handleHabitReset} />
              <WeeklySummary history={journalHistory} />
            </div>
          )}
        </div>
      </main>
    </>
  );
}


// ==================================================================
// 3. TAB BUTTON HELPER COMPONENT
// ==================================================================
interface TabButtonProps {
  icon: React.ReactNode;
  label: string;
  isActive: boolean;
  onClick: () => void;
}
function TabButton({ icon, label, isActive, onClick }: TabButtonProps) {
  return (
    <button
      onClick={onClick}
      className={`flex-1 flex items-center justify-center gap-2 px-3 py-2 rounded-md font-semibold transition-all ${
        isActive
          ? 'bg-white text-indigo-700 shadow-md'
          : 'text-slate-600 hover:bg-slate-100'
      }`}
    >
      {icon}
      <span>{label}</span>
    </button>
  );
}


// ==================================================================
// 4. HELPER COMPONENTS (FROM YOUR OTHER FILES)
// ==================================================================

// --- Tag Component ---
function Tag({ label, type }: { label: string, type: 'sentiment' | 'label' | 'activity' }) {
  const colorClasses: { [key: string]: { [key: string]: string } } = {
    sentiment: {
      POSITIVE: 'bg-green-100 text-green-800',
      NEGATIVE: 'bg-rose-100 text-rose-800',
      NEUTRAL: 'bg-slate-100 text-slate-800',
    },
    label: {
      Anxiety: 'bg-violet-100 text-violet-800',
      Fatigue: 'bg-slate-100 text-slate-800',
      Sadness: 'bg-blue-100 text-blue-800',
      Work: 'bg-amber-100 text-amber-800',
      Family: 'bg-pink-100 text-pink-800',
      default: 'bg-indigo-100 text-indigo-800',
    },
    activity: {
      default: 'bg-orange-100 text-orange-800 border border-orange-200',
    },
  };
  let color;
  if (type === 'sentiment') color = colorClasses.sentiment[label] || colorClasses.sentiment.NEUTRAL;
  else if (type === 'label') color = colorClasses.label[label] || colorClasses.label.default;
  else color = colorClasses.activity.default;
  return <span className={`px-3 py-1 text-sm font-medium rounded-full ${color}`}>{label}</span>;
}

// --- InsightCard Component ---
function InsightCard({ analysis }: { analysis: AnalysisResult }) {
  const strategy = getPersonalizedStrategy(analysis);
  return (
    <div className="mt-6 bg-indigo-50 border border-indigo-200 rounded-lg p-4">
      <h3 className="font-bold text-lg text-indigo-800 flex items-center">{strategy.emoji} {strategy.title}</h3>
      <p className="text-indigo-700 mt-2">{strategy.text}</p>
    </div>
  );
}

// --- CorrelationCard Component ---
function CorrelationCard({ analysis }: { analysis: AnalysisResult }) {
  if (!analysis.activities || analysis.activities.length === 0) return null;
  let insightText = null;
  if (analysis.sentiment === 'POSITIVE' && analysis.activities.includes('Exercise')) insightText = "Great job on exercising! It seems to have a positive impact on your mood.";
  else if (analysis.sentiment === 'POSITIVE' && analysis.activities.includes('Social')) insightText = "Connecting with others is powerful. It looks like it boosted your spirits today!";
  else if (analysis.labels.includes('Fatigue') && analysis.activities.includes('Work')) insightText = "We noticed you logged 'Work' and are feeling fatigued. Remember to take regular breaks to recharge.";
  if (!insightText) return null;
  return (
    <div className="mt-4 bg-green-50 border border-green-200 rounded-lg p-4">
      <h3 className="font-bold text-lg text-green-800 flex items-center">📈 Correlation Insight</h3>
      <p className="text-green-700 mt-2">{insightText}</p>
    </div>
  );
}

