'use client';

import { useEffect, useState } from 'react';
import { Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';

// Register the components Chart.js needs
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

// Define the structure of our historical entries
interface HistoryEntry {
  date: string;
  sentiment: 'POSITIVE' | 'NEGATIVE' | 'NEUTRAL' | string;
}

export default function MoodChart() {
  const [chartData, setChartData] = useState<any>({ datasets: [] });
  const [hasEnoughData, setHasEnoughData] = useState(false);

  useEffect(() => {
    // We need to check for window to safely access localStorage
    if (typeof window === 'undefined') {
      return;
    }

    const history: HistoryEntry[] = JSON.parse(localStorage.getItem('aura-history') || '[]');
    const sevenDaysAgo = new Date();
    sevenDaysAgo.setDate(sevenDaysAgo.getDate() - 7);

    const recentEntries = history.filter(entry => new Date(entry.date) >= sevenDaysAgo);

    if (recentEntries.length < 2) {
      setHasEnoughData(false);
      return; // Need at least 2 points to draw a line
    } 

    setHasEnoughData(true);
    // Format data for the chart
    const labels = recentEntries.map(entry => new Date(entry.date).toLocaleDateString('en-US', { month: 'short', day: 'numeric' }));
    const dataPoints = recentEntries.map(entry => {
      if (entry.sentiment === 'POSITIVE') return 1;
      if (entry.sentiment === 'NEGATIVE') return -1;
      return 0; // NEUTRAL
    });

    setChartData({
      labels,
      datasets: [
        {
          label: 'Mood Trend',
          data: dataPoints,
          borderColor: 'rgb(59, 130, 246)',
          backgroundColor: 'rgba(59, 130, 246, 0.5)',
          tension: 0.1, // Makes the line slightly curved
        },
      ],
    });
  }, []);

  if (!hasEnoughData) {
    return (
      <div className="my-8 bg-white p-6 rounded-lg shadow-md">
        <div className="text-center p-4 bg-gray-100 rounded-lg">
          <p className="text-gray-600">Journal for a few more days to see your mood trend chart!</p>
        </div>
      </div>
    );
  }

  const chartOptions = {
    responsive: true,
    plugins: {
      legend: { display: false },
      title: { display: true, text: 'Your Mood Over the Last 7 Days' },
    },
    scales: {
      y: {
        ticks: {
          // This callback formats the Y-axis labels
          callback: function(value: any) {
            if (value === 1) return 'Positive';
            if (value === -1) return 'Negative';
            if (value === 0) return 'Neutral';
            return null;
          }
        },
        // Set explicit min/max to keep the labels consistent
        min: -1.5,
        max: 1.5,
      },
    },
  };

  return (
    <div className="my-8 bg-white p-6 rounded-lg shadow-md">
      <Line options={chartOptions} data={chartData} />
    </div>
  );
}